<template>
  <img
    :src="src"
    :style="responsiveStyle"
    v-if="mode === 'responsive'"
  >
  <div
    :style="imageStyle"
    class="image"
    v-else
  >
    <template v-if="!src">
      {{filtedUsername}}
      <slot/>
    </template>
  </div>
</template>

<script>
// 
const modeArr = ['scaleToFill', 'aspectFit', 'aspectFill', 'heightFix', 'widthFix', 'responsive']

export default {
  name: 'ImagePro',
  props: {
    // 
    src: {
      type: String
    },
    // 
    mode: {
      type: String,
      default: 'aspectFill',
      validator (value) {
        return modeArr.includes(value)
      }
    },
    // src
    username: {
      type: String,
      default: ''
    },
    // 
    size: {
      type: Number,
      default: 50
    },
    // 
    width: {
      type: Number
    },
    // 
    height: {
      type: Number
    },
    // 
    color: {
      type: String,
      default: '#FFFFFF'
    },
    // 
    backgroundColor: {
      type: String,
      default: ''
    },
    // 
    radius: {
      type: Number,
      default: 0
    },
    // 
    customStyle: {
      type: Object
    }
  },
  computed: {
    // 
    imageWidth () {
      return this.width || this.size
    },
    // 
    imageHeight () {
      return this.height || this.size
    },
    // 
    backgroundSize () {
      let backgroundSize = ''
      switch (this.mode) {
        // 
        case 'aspectFit':
          backgroundSize = 'contain'
          break
        // 
        case 'aspectFill':
          if (this.imageSize.width && this.imageSize.height) {
            let imageWidthPercent = 100
            let imageHeightPercent = 100
            // 
            if (this.imageSize.width < this.imageSize.height) {
              imageHeightPercent = this.imageSize.height / this.imageSize.width * 100
            } else {
              imageWidthPercent = this.imageSize.width / this.imageSize.height * 100
            }

            // 
            if (this.imageHeight > this.imageWidth) {
              imageWidthPercent *= (this.imageHeight / this.imageWidth)
            } else {
              imageHeightPercent *= (this.imageWidth / this.imageHeight)
            }

            //  100% 
            if (imageWidthPercent < imageHeightPercent) {
              imageHeightPercent *= 100 / imageWidthPercent
              imageWidthPercent = 100
            } else {
              imageWidthPercent *= 100 / imageHeightPercent
              imageHeightPercent = 100
            }
            backgroundSize = `${imageWidthPercent}% ${imageHeightPercent}%`
          } else {
            backgroundSize = null
          }
          break
        case 'heightFix':
          backgroundSize = 'auto 100%'
          break
        case 'widthFix':
          backgroundSize = '100% auto'
          break
        default:
          //  image 
          backgroundSize = `${this.imageWidth}px ${this.imageHeight}px`
      }

      return backgroundSize
    },
    // 
    fontSize () {
      if (this.filtedUsername && this.filtedUsername.length >= 2) {
        return Math.floor(this.imageWidth / (this.filtedUsername.length + 0.4)) + 'px'
      } else {
        return Math.floor(this.imageWidth / 2.3) + 'px'
      }
    },
    // mode=responsive
    responsiveStyle () {
      const style = {
        borderRadius: this.radius + '%',
        maxWidth: '100%',
        height: 'auto'
      }
      return Object.assign({}, style, this.customStyle)
    },
    // 
    imageStyle () {
      const style = {
        color: this.color,
        width: this.imageWidth + 'px',
        borderRadius: this.radius + '%',
        height: this.imageHeight + 'px',
        lineHeight: Math.floor(this.imageHeight * 0.98) + 'px',
        backgroundSize: this.backgroundSize,
        backgroundColor: this.src ? null : this.backgroundColor,
        fontSize: this.fontSize,
        backgroundImage: this.src ? `url(${this.src})` : null
      }
      return Object.assign({}, style, this.customStyle)
    },
    // 
    filtedUsername () {
      let parts = this.username.split(/[ -]/)
      let initials = ''
      for (let i = 0; i < parts.length; i++) {
        initials += parts[i].charAt(0)
      }

      if (initials.length > 3 && initials.search(/[A-Z]/) !== -1) {
        initials = initials.replace(/[a-z]+/g, '')
      }

      initials = initials.substr(0, 3).toUpperCase()
      return initials
    }
  },
  data () {
    return {
      imageSize: {}
    }
  },
  methods: {
    getImageSize () {
      if (this.src) {
        const img = new Image()
        img.src = this.src
        img.onerror = () => {
          this.$emit('error')
        }

        img.onload = () => {
          this.imageSize = {
            width: img.width,
            height: img.height
          }
          img.onload = null // 
        }
      }
    }
  },
  mounted () {
    this.getImageSize()
  }
}
</script>

<style scoped>
.image {
  text-align: center;
  font-weight: bold;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: center;
}

.responsive-img {
  max-width: 100%;
  height: auto;
}
</style>
